import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { CommandsProvider } from '../../providers/commands/commands';


/**
 * Generated class for the DetalleConsultaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detalle-consulta',
  templateUrl: 'detalle-consulta.html',
})
export class DetalleConsultaPage {

  des: any; 
  descuentos: any;
  prueba: string;
  idioms: any[] = [];
  x : any;

  numCuenta: number;
  tipoCuenta: number;
  public lista1 : Array<string>;
  public message: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, public http: HttpProvider, public utils: UtilsProvider,
              public loading: LoadingController, public command: CommandsProvider) {
    
    this.storage.get('noCuenta').then((val) => {  
       this.numCuenta = val;
    });
    this.storage.get('tipoCuenta').then((val) => {  
      this.tipoCuenta = val;
   });
   
  }

  ionViewDidLoad() {
    //obtención de la lista guardada en sesión para no volver hacer la consulta
    this.storage.get('lista1').then((val) =>    { if(val != null ){ this.lista1 = val; } });
  }

  ionViewDidEnter() {

    if(this.lista1 == null || this.lista1 == undefined){
      this.detalleCuenta();

      //Carga del metodo Loanding
      this.presentLoadingDefault();
    }
    
  }

  //Metodo Loanding
  presentLoadingDefault() {
    let loading = this.loading.create({
      content: 'Please wait...'
    });
  
    loading.present();
  
    setTimeout(() => {
      loading.dismiss();
    }, 2000);
  }

  consultaSaldo(){
    this.navCtrl.push("ConsultaSaldosPage");
  }

  productOverView(){
    this.navCtrl.push("ConsultaPage");
  }

  Service(){
    this.navCtrl.push("Tab1Page");
  }

  abrir(){
    this.navCtrl.push("RutasConsultasPage");
  }

  movimientos(){
    this.navCtrl.push("FiltroPage", { flagFiltro: 2 });
  }

   private detalleCuenta() {   
    
     //var command = 'E2LSCR210';
     var command :string[] =  this.utils.getCommands(this.command.E2LSCR210);

     var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID=' + this.utils.getSessionId() + '&E2EW=' + this.utils.separator + 'Enviar' + this.utils.separator + this.utils.separator + 'E2SECR61P' + this.utils.separator + '0' + this.utils.separator + this.utils.separator + '&PAR=' + this.numCuenta + '|' + this.tipoCuenta + '+%BA20+%BA+%BA01282014+%BA12282017+%BA+%BA+%BA+%BA+%BA+%BA+%BA+%BA+%BA+%BA';

     this.http.callServer(param).then(res => {
       this.message = res.message;

       if (this.message != '' && typeof this.message !== "undefined") {
        this.utils.alertServidor(this.message);
       } else {
         this.lista1 = res.lista1;
         this.lista1.splice(0,1);
         this.storage.set("lista1", this.lista1);
       }

     },
       error => {
         console.log(error);
       });
   }

   back(){
     this.navCtrl.push("ProductOverViewPage");
   }

   logout(){
    this.utils.presentConfirm("LoginPage");
   }

}



